import React, { Component } from 'react'
import {CopyToClipboard} from "react-copy-to-clipboard";
import {Link} from "react-router-dom"; 
import {withStyles} from "@material-ui/styles";
import classNames from "classnames";
import styles from "./styles/ColorBoxStyles"

class ColorBox extends Component {
    constructor(props){
        super(props);
        this.state = { copied: false }
        this.changeCopyState = this.changeCopyState.bind(this);
    }
    changeCopyState(){
        this.setState(curState => (
            {copied : true}
        ), () => {
            setTimeout(() => this.setState({copied: false}), 1500);
        })
    }
    propogationStop(evt){
        evt.stopPropagation();
    }
    render() {
        const {name, background, moreUrl, showingFullPalette , classes} = this.props;
        const {copied} = this.state;
        return (
            <CopyToClipboard text = {background} onCopy = {this.changeCopyState}>
                <div style = {{background: background} }className = {classes.ColorBox}>
                    <div className = {classNames(classes.copyOverlay, {[classes.showOverlay]: copied})} style = {{background: background}} />
                    <div className = {classNames(classes.copyMessage, {[classes.showMessage]: copied})}>
                        <h1>copied!</h1>
                        <p className = {classes.copyText}>{background}</p>
                    </div>
                    <div>
                        <div className = {classes.boxContent}>
                            <span className = {classes.colorName}>{name}</span>
                        </div>
                        <button className = {classes.copyButton}>Copy</button>
                    </div>
                    { showingFullPalette &&
                    (<Link to = {moreUrl} onClick = {this.propogationStop}>
                        <span className = {classes.seeMore}>MORE</span>
                    </Link>)}
                </div>
            </CopyToClipboard>
        )
    }
}

export default withStyles(styles)(ColorBox);
